.page-header {
    background-color: aqua;
}

nav {
    display: flex;
    align-items: center;
    padding: 10px;
    padding-left: 25px;
    padding-right: 140px;
}
.nav__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.nav .nav__toggle {
    position: absolute;
    right: 20px;
    width: 43px;
    height: 43px;
    background: url("../img/svg/buttonMenu.svg") no-repeat top left;
    border: none;
    cursor: pointer;
    z-index: 2;
    display: none;
}
.nav__toggle--v {
    display: block;
}


.nav-list {
    list-style: none;
}
.nav-list__item {
    font-size: 22px;
    line-height: 1;
    text-decoration: none;
    color: white;
}
.nav-list__link:hover {
    color: #828282;
}
.nav__block {
    display: block;
}